<template>
	<view class="container" v-if="detailsData.titlepic">
		<view class="header">
			<view class="header-t">
				<image :src="detailsData.titlepic" mode="" class="logo"></image>
				<view class="header-t-r">
					<view class="title">
						{{detailsData.title}}
					</view>
					<view class="tags">
						<view class="item" v-for="item in tags" :key="item">
							{{item}}
						</view>
					</view>
				</view>
			</view>
			<view class="header-b">
				{{detailsData.smalltext}}
			</view>
		</view>
		<view class="info case">
			<view class="tit">
				<text class="tit-name">专业基本情况</text>
			</view>
			<view class="box">
				<view class="item">
					现专业代码：{{detailsData.zhuanyedaima}}
				</view>
				<view class="item">
					基本学制：{{detailsData.jibenxuezhi}}
				</view>
				<view class="item">
					原专业代码：{{detailsData.yzydm}}
				</view>
				<view class="item">
					原专业名称：{{detailsData.yzymc}}
				</view>
				<view class="item">
					对口高职专业：{{detailsData.duikougaozhi}}
				</view>
				<view class="item">
					对口本科专业：{{detailsData.duikoubenke}}
				</view>
				<view class="item">
					培养人才目标专业：{{detailsData.peiyangmubiao}}
				</view>
			</view>
		</view>
		<view class="vista case">
			<view class="tit">
				<text class="tit-name">专业就业前景</text>
			</view>
			<view class="content">
				<mp-html :content="vistaData"></mp-html>
			</view>
		</view>
		<view class="introduce case">
			<view class="tit">
				<text class="tit-name">专业详情介绍</text>
			</view>
			<view class="content">
				<mp-html :content="introduceData"></mp-html>
			</view>
		</view>
		<DetailsSuspend :majorName="detailsData.title"/>
	</view>
</template>

<script>
	import {
		getMajorDetails
	} from '@/api/major.js'
	import DetailsSuspend from '@/components/library/DetailsSuspend.vue'
	import {
		websiteUrl
	} from '@/utils/config.js'
	export default {
		data() {
			return {
				id: null,
				detailsData: {},
				tags: [],
				vistaData: null,
				introduceData: null,
				ttData: [{
						ttid: 29,
						name: '农林牧渔大类'
					},
					{
						ttid: 30,
						name: '资源环境与安全大类'
					},
					{
						ttid: 31,
						name: '能源动力与材料大类'
					},
					{
						ttid: 32,
						name: '装备制造大类'
					},
					{
						ttid: 33,
						name: '生物与化工大类'
					},
					{
						ttid: 34,
						name: '轻纺食品大类'
					},
					{
						ttid: 35,
						name: '交通运输大类'
					},
					{
						ttid: 36,
						name: '电子信息大类'
					},
					{
						ttid: 37,
						name: '医药卫生大类'
					},
					{
						ttid: 38,
						name: '财经商贸大类'
					},
					{
						ttid: 39,
						name: '旅游服务大类'
					},
					{
						ttid: 40,
						name: '文化艺术大类'
					},
					{
						ttid: 41,
						name: '新闻传播大类'
					},
					{
						ttid: 42,
						name: '教育与体育大类'
					},
					{
						ttid: 43,
						name: '公共管理与服务大类'
					},
					{
						ttid: 44,
						name: '土木水利大类'
					}
				]

			};
		},

		components: {
			DetailsSuspend
		},
		onLoad(e) {
			getMajorDetails(e.id).then(({
				data
			}) => {
				data.titlepic = websiteUrl + data.titlepic
				let currTtData = this.ttData.find(v => {
					return v.ttid === Number(data.ttid)
				})
				this.tags = [currTtData.name, ...data.zyshuxing.split('|').filter(item => item.length > 0)]
				this.vistaData = data.zhuanyeqianjing.replace(/[\\]/g, '')
				this.introduceData = data.newstext.replace(/[\\]/g, '')
				this.detailsData = data
				uni.setNavigationBarTitle({
					title: data.title
				})
			})

		},
		// 点击分享按钮
		onShareAppMessage(res) {
			
			if (res.from === 'botton') {
				console.log(res.target);
			}
			let pages = getCurrentPages() // 获取栈实例
			let currentRoute  = pages[pages.length-1].route; // 获取当前页面路由
			let currentPage = pages[pages.length-1]['$page']['fullPath']
			return {
				title: this.detailsData.title,
				path: currentPage,
				imageUrl: this.detailsData.titlepic,
				content: this.detailsData.smalltext,
				desc: this.detailsData.smalltext
			}
			
		}
	}
</script>

<style lang="scss">
	page {
			background-color: #fff;
		}
	.container {
		padding: 20px 8px;
		padding-bottom: 50px;
	}

	.header {
		padding-bottom: 15px;

		.header-t {
			display: flex;

			.logo {
				width: 64px;
				height: 64px;
				border-radius: 50%;
				margin-right: 8px;
			}

			.header-t-r {
				display: flex;
				flex-direction: column;
				justify-content: center;
				.title {
					font-weight: 700;
					font-size: 22px;
					color: #333;
				}

				.tags {
					.item {
						display: inline-block;
						font-size: 12px;
						margin-top: 6px;
						margin-right: 8px;
						padding: 1px 8px;
						color: #fff;
						border-radius: 20px;
						background-color: #0095fd;
					}
				}
			}
		}

		.header-b {
			margin-top: 15px;
			font-size: 15px;
			color: #555;
			text-align: justify;
			line-height: 20px;
		}
	}

	.case {
		margin-top: 20px;
	}

	.tit {
		font-size: 18px;
		font-weight: 700;
		border-bottom: 2px solid #ddd;

		.tit-name {
			border-bottom: 2px solid #0095fd;
		}
	}

	.info {
		.box {
			.item {
				font-size: 16px;
				color: #555;
				margin-top: 8px;
			}
		}
	}

	.content {
		font-size: 16px;
		color: #555;
		line-height: 20px;
		text-align: justify;
	}
</style>
